<template>
  <header>
      <div v-on:click="onHome">Home</div>
      <div v-on:click="onChallenge">Challenge</div>
      <div v-on:click="onLogout">Logout</div>
  </header>
</template>

<style>
  header {
    display: flex;
    flex-direction: row;
    width: 100%;
  }

  header > div {
    margin-left: 1em;
    cursor: pointer;
    font-size: 1.5em;
    background-color: whitesmoke;
    padding: 0.2em 0.8em;
  }
</style>

<script>
export default {
  name: 'SiteHeader',
  methods: {
    onHome() {
      this.$router.push("/home");
    },
    onChallenge() {
      this.$router.push("/challenge");
    },
    onLogout() {
      this.$axios.put('/api/auth/logout').then(() => {
        localStorage.removeItem('auth-token');
        this.$router.push("/signin");
      })
    }
  }
}
</script>
