<template>
  <div>
    <h1 class="title">The movie challenge</h1>
    <h2>Sign in with:</h2>
    <div 
      v-on:click="clickGoogleAuth"
      class="btn-cnt">
      <div
        class="google-label" 
        v-on:click="clickGoogleAuth"
      >Google</div>
    </div>
  </div>
</template>

<style>
  .title {
    font-size: 3em;
    margin-bottom: 3em;
  }
  .btn-cnt {
    display: flex;
    flex-direction: column;
    justify-content: baseline;
    cursor: pointer;
  }

  .google-label {
    align-self: center;
    display: flex;
    flex-direction: row;
    align-items: center;
    font-size: 2em;
  }

  .google-label::before {
    content: " ";
    background: url(https://upload.wikimedia.org/wikipedia/commons/5/53/Google_%22G%22_Logo.svg);
    height: 50px;
    width: 50px;
    display: block;
    background-size: 50px;
    margin-right: 0.5em;
  }
</style>

<script>
export default {
  name: 'Signin',
  data () {
    return {

    }
  },

  methods: {
    clickGoogleAuth(){
      this.$gAuth.getAuthCode()
      .then(authCode => {
        // On success
        console.log(authCode);
        return this.$axios.post(
          '/api/auth/google', 
          { code: authCode }
        )
      })
      .then(response => {
        console.log('Auth token received', response.auth_token); //
        let auth_token = response.auth_token;
        localStorage.setItem('auth-token', auth_token);
        console.log('Checking localStorage auth_token', localStorage.getItem('auth-token'));//

        this.$router.push('/home');
      })
      .catch(error => {
        console.error(error);
      })
    },
  }
}
</script>
