import Vue from 'vue'
import App from './App.vue'
import Home from './components/Home.vue'
import Signin from './components/Signin.vue'
import Challenge from './components/Challenge.vue'
import VueRouter from 'vue-router'
import GAuth from 'vue-google-oauth2'

Vue.config.productionTip = false;

const gauthOption = {
  clientId: '99787422901-8i0bebl58png79lcl1vqushprlmufc20.apps.googleusercontent.com',
  scope: 'profile email',
  prompt: 'select_account'
};

Vue.use(GAuth, gauthOption);
Vue.use(VueRouter);

import axios from 'axios';
Vue.prototype.$axios = axios.create({
  baseURL: 'https://d0s8asa2p7.execute-api.us-east-1.amazonaws.com/MovieQuizAPI_v1'
});

let isAuthenticated = (from, to, next) => {
  if (!localStorage.getItem('auth-token')) {
    next('/signin');
  } else {
    next();
  }
};

 let isNotAuthenticated = (from, to, next) => {
   if (!localStorage.getItem('auth-token')) {
     next();
   } else {
     next('/');
   }
 };

new Vue({
  el: '#app',
  components: { App },
  template: '<App/>',
  axios,
  router: new VueRouter({
    routes: [
      {
        path: '/',
        redirect: '/home'
      },
      {
        path: '/signin',
        name: 'Signin',
        beforeEnter: isNotAuthenticated,
        component: Signin
      },
      {
        path: '/home',
        name: 'Home',
        beforeEnter: isAuthenticated,
        component: Home
      },
      {
        path: '/challenge',
        name: 'Challenge',
        beforeEnter: isAuthenticated,
        component: Challenge
      },
    ],
  })
})
