<template>
  <div>
    <site-header/>
    <h1>Challenge</h1>
    <div
        v-if="!!question"
        class="quest-img-cont">
        <div
            v-for="(src, i) in question.images"
            v-bind:key="'image-'+i"
        >
            <img
                v-bind:src="src | fullImgSrc"
                v-bind:alt="'image-'+i"
            />
        </div>
    </div>
    <div
        v-else-if="!!waitingInfo"
    >
        <div>{{'Users count: ' + waitingInfo.count}}</div>
        <div>{{'Users required: ' + waitingInfo.required}}</div>
    </div>
    <div v-else>
        Loading ...
    </div>

    <h1 
        v-if="!!question"
        id="counter"
    >{{ question.time_left | timeFormat}}
    </h1>

    <div v-if="!!question">
        <div class="guess-film" >{{'Guess the film title ('+question.film_title+')'}}</div>
        <input
            v-model="answer"
            v-bind:disabled="question.time_left === 0 || answerSent"
            type="text"
            id="answer-input"
        />
        <button
            v-bind:disabled="question.time_left === 0 || answerSent"
            v-on:click="sendAnswer"
        >Send</button>
    </div>
  </div>
</template>

<style>
    .quest-img-cont {
        display: flex;
        flex-direction: row;
        justify-content: center;
    }

    .guess-film {
        margin-bottom: 0.8em;
    }

    #answer-input, #answer-input ~ button {
        font-size: 1.5em;
    }
</style>

<script>
import SiteHeader from "./SiteHeader"
import Pusher from "pusher-js";

var pusher = new Pusher('2e0abd87569c657457fa', {
    cluster: 'eu',
    forceTLS: true
});

console.log(pusher);

export default {
  name: 'Challenge',
  components: { SiteHeader },
  data() {
      return {
          question: undefined,
          waitingInfo: undefined,
          quest_id: undefined,
          result: undefined,
          answer: "",
          answerSent: false
      }
  },
  created() {
      this.startChallenge();
  },
  beforeDestroy() {
      clearInterval(this.timer);
  },
  methods: {
      sendAnswer() {
        this.answerSent = true;
        this.$axios.post('/api/quiz', {
            answer: this.answer ? this.answer : " ",
            quest_id: this.quest_id
        }).catch(err => {
            console.error(err);
        });
      },
      startChallenge() {
          this.question = undefined;
          this.waitingInfo = undefined;
          this.quest_id = undefined;
          this.result = undefined;
          this.answer = "";
          this.answerSent = false;

          this.$axios.get("/api/quiz").then(resp => {
            console.log(resp);
            if (resp === null) {
                 this.$axios.get("/api/quiz").then(r => {
                     if (r.body.question) {
                        this.question = r.body.question;
                        this.quest_id = r.body.question.quest_id;
                    }
                 });
                 return;
            }

            if (resp.body.message === 'Question was closed!') {
                this.$axios.get("/api/quiz").then(r => {
                     if (r.body.question) {
                        this.question = r.body.question;
                        this.quest_id = r.body.question.quest_id;
                    } else {
                        this.waitingInfo = r.body;
                        this.quest_id = this.waitingInfo.quest_id;
                    }
                });
                return;
            }

            if (resp.body.question) {
                this.question = resp.body.question;
                this.quest_id = resp.body.question.quest_id;
            } else {
                this.waitingInfo = resp.body;
                this.quest_id = this.waitingInfo.quest_id;
            }
        });

        this.timer = setInterval(() => {
            if (!this.question) return;

            if (this.question.time_left <= 0) {
                clearInterval(this.timer);
                this.sendAnswer(this.answer);
                return;
            }
            this.question.time_left--;
        }, 1000);
      }
  },
  filters: {
      fullImgSrc(src) {
          return 'https://image.tmdb.org/t/p/w300_and_h450_bestv2/'+src;
      },
      timeFormat(seconds) {
          let min = Math.floor(seconds / 60);
          let sec = '' + (seconds - 60 * min);
          if (sec.length === 1) sec = '0' +  sec;
          return min + ':' + sec;
      }
  },
  watch: {
      quest_id(val, oldVal) {
          if (!val) return;

          console.log('Old', oldVal);
          console.log('Current', val);

          let channel = pusher.subscribe(val);

          channel.bind("new-member", data => {
              console.log("new-member", data);
              this.waitingInfo = data;
          });
          channel.bind("question-started", data => {
              console.log("question-started", data);
              this.question = data.question;
              this.quest_id = data.question.quest_id;
          });
          channel.bind("question-finished", data => {
              pusher.unsubscribe(data.quest_id);
              console.log("question-finished", data.film_title);

              let cont = confirm(data.film_title);

              if (cont) {
                  this.startChallenge();
              } else {
                  this.$router.push('/home');
              }
          });
      }
  }
}
</script>